import Vue from 'vue'
import App from './App'
import router from './router'
import '@/assets/index.css'
import { loading } from '@jiaminghi/data-view'
// import tween from 'tweenjs'
// Vue.use(tween)
Vue.use(loading)
Vue.config.productionTip = false
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



