<template>
  <div class="container" id="container">
    <dv-loading v-if="!modelsState" class="loading">{{modelsSpeed + '%'}}</dv-loading>
    <div id="webgl" ref="webgl"></div>
  </div>
</template>

<script>
import * as THREE from "three";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls";
import { FBXLoader } from 'three/examples/jsm/loaders/FBXLoader.js';
import { getCoordinate } from '../components/getCoordinate';
let scene, camera, renderer, controls;
let innerWidth = document.documentElement.clientWidth;
let innerHeight = document.documentElement.clientHeight;
let fbxLoader = new FBXLoader();
//场景分组
let sceneGroup = new THREE.Group();
let clock = new THREE.Clock();
let guanluModels;
export default {
  name: 'home',
  data () {
    return {
      //模型加载状态
      modelsState: false,
      //模型加载进度
      modelsSpeed: 0,
      //相机视角
      cameraPosition: { x: 1220, y: 814, z: 250 },
      //鼠标移动时间戳
      lastMove: new Date().getTime(),
      //鼠标间隔时间
      lockTime: 1000,
      //点光源
      pointLightData: [
        {x: 580, y: 200, z: 350, color: '#848168', distance: 1000},
        {x: -346, y: 200, z: 1067, color: '#848168', distance: 1000},
        {x: -355, y: 200, z: 267, color: '#848168', distance: 1000},
        {x: 156, y: 200, z: -842, color: '#848168', distance: 1000}
      ],
      //节点
      buildNode:[
        {img: '1', x: 490, y: 0, z: 654, name: '鸳鸯湖电厂'},
        {img: '2', x: -133, y: 0, z: 1540, name: '中试基地'},
        {img: '3', x: -390, y: 0, z: 1163, name: '五恒化学'},
        {img: '4', x: 0, y: 0, z: 941, name: '博安化工'},
        {img: '5', x: -204, y: 0, z: 923, name: '恒泰科技'},
        {img: '6', x: -446, y: 0, z: 738, name: '鸣川'},
        {img: '7', x: -190, y: 0, z: 591, name: '恒泰'},
        {img: '8', x: 41, y: 0, z: 579, name: '享誉'},
        {img: '9', x: 87, y: 0, z: 317, name: '污水处理厂'},
        {img: '10', x: -115, y: 0, z: 343, name: '宁顺'},
        {img: '11', x: -401, y: 0, z: 324, name: '瑞鼎'},
        {img: '12', x: -383, y: 0, z: -421, name: '鼎泰'},
        {img: '9', x: -30, y: 0, z: -404, name: '污水处理厂'},
        {img: '13', x: 342, y: 0, z: -567, name: '中基国能'},
        {img: '14', x: 209, y: 0, z: -864, name: '合成生物'},
        {img: '15', x: 64, y: 0, z: -1165, name: '派可威生物'},
        {img: '16', x: -261, y: 0, z: -927, name: '德中化学'},
        {img: '17', x: -212, y: 0, z: -1323, name: '宝延一期'},
        {img: '18', x: -28, y: 0, z: -1388, name: '宝延二期'},
      ]
    }
  },
  mounted() {
    this.$nextTick(()=>{
      this.init()
    })
  },
  methods:{
    init(){
      this.initScene()
      this.initCamera()
      this.initRenderer()
      this.initLight()
      this.initControls()
      this.initContent()
      this.render()
      getCoordinate(renderer, camera, scene)
      window.addEventListener('resize', this.onWindowResize, false)
      window.addEventListener('mousemove', this.onMousemove );
    },
    //场景
    initScene(){
      scene = new THREE.Scene();
    },
    // 相机
    initCamera() {
      camera = new THREE.PerspectiveCamera(45, innerWidth / innerHeight, 1, 100000)
      camera.position.set(this.cameraPosition.x, this.cameraPosition.y, this.cameraPosition.z)
    },
    // 渲染器
    initRenderer() {
      renderer = new THREE.WebGLRenderer({antialias: true});
      // 设置渲染器的像素比例，按照设备
      renderer.setPixelRatio(window.devicePixelRatio);
      // 渲染范围
      renderer.setSize(innerWidth, innerHeight);
      // 开启阴影支持
      renderer.shadowMap.enabled = true;
      // 阴影类型
      renderer.shadowMap.type = THREE.PCFSoftShadowMap;
      document.getElementById("webgl").appendChild(renderer.domElement);
    },
    // 灯光
    initLight(){
      this.ambientLight();
      this.pointLight();
    },
    //环境光
    ambientLight(){
      const light = new THREE.AmbientLight('#B5D3F2', 0.8);
      const directionalLight = new THREE.DirectionalLight('#666', 0.8);
      scene.add(light);
      scene.add(directionalLight);
    },
    //点光源
    pointLight(){
      // 加入一个小球体来表示点光源位置
      // let sphereGeometry = new THREE.SphereGeometry(50, 8, 8);
      // let sphereMaterial = new THREE.MeshBasicMaterial( {color: '#FF2200'} );
      // let lightMesh = new THREE.Mesh(sphereGeometry, sphereMaterial);

      this.pointLightData.map(res =>{
        let pointLight = new THREE.PointLight(res.color, 0.6);
        pointLight.distance = res.distance;
        pointLight.position.set(res.x, res.y, res.z);
        // pointLight.add(lightMesh);
        scene.add(pointLight);
      })
    },
    // 控制器
    initControls() {
      controls = new OrbitControls(camera, renderer.domElement);
      controls.maxDistance = 10000;
      controls.minDistance = -100;
      controls.minPolarAngle = 0;
      controls.maxPolarAngle = Math.PI * (90 / 190);
      controls.rotateSpeed = 0.5;
      controls.mouseButtons={
        LEFT: THREE.MOUSE.PAN,
        MIDDLE: THREE.MOUSE.DOLLY,
        RIGHT: THREE.MOUSE.ROTATE
      }
    },
    // 场景中的内容
    initContent() {
      this.initSkyScene()
      this.initModel()
      this.pipeline()
      this.createLableSprite_jcy()
    },
    //天空场景
    initSkyScene() {
      let skyScene = new THREE.CubeTextureLoader()
        .setPath('/static/imgs/skyboxs/')
        .load([
          "right.png",
          "left.png",
          "top.png",
          "down.png",
          "back.png",
          "front.png"
        ]);
      scene.background = skyScene
    },
    // 窗口变动触发的方法
    onWindowResize() {
      camera.aspect = innerWidth / innerHeight;
      camera.updateProjectionMatrix();
      renderer.setSize(innerWidth, innerHeight);
      console.log(innerWidth)
    },
    // 更新场景
    update() {
      if(guanluModels){
        guanluModels.offset.x -= 0.007
      }
      this.listenMouseMove()
      controls.update()
    },
    // 循环渲染
    render() {
      // console.log(camera.position)
      // console.log(sceneGroup.position)
      requestAnimationFrame(this.render);
      renderer.render(scene, camera);
      this.update();
    },
    //加载模型
    initModel(){
      let _this = this;
      fbxLoader.load('/static/models/city.fbx', (fbx) => {
        console.log(fbx)
        fbx.children.map(child =>{
          if(child.name == 'jianzhu'){
            //屋顶纹理
            child.material[1] = new THREE.MeshPhysicalMaterial({
              emissive: '#181923',
              emissiveIntensity: 0,
              metalness: 0.5,
              roughness: 0,
              learCoat: 0.5,
              reflectivity: 0.7,
              side: THREE.DoubleSide
            })
            //建筑纹理
            child.material[0] = new THREE.MeshPhysicalMaterial({
              emissive: child.material[0].color,
              emissiveMap: child.material[0].map,
              emissiveIntensity: 2,
              metalness: 0.5,
              roughness: 0,
              learCoat: 0.5,
              reflectivity: 0.7,
              side: THREE.DoubleSide
            })
          }
          //道路
          if(child.name == 'daolu'){
            child.material.color = new THREE.Color('#1F314F')
          }
          //区块
          if(child.name == 'qukuai'){
            child.material.color = new THREE.Color('#1E3854')
          }
          //管道
          if(child.name == 'guanlu'){
            child.material.opacity = 0
            child.material.transparent = true
          }
        })
        sceneGroup.add(fbx)
        sceneGroup.position.set(-800, -200, -650)
        scene.add(sceneGroup)
      }, (xhr =>{
        let num = Math.floor(xhr.loaded / xhr.total * 100)
        _this.modelsSpeed = num
        _this.modelsState = num == 100 ? true : false
      }));
    },
    //道路动画
    pipeline(){
      let loader = new THREE.TextureLoader()
      guanluModels = loader.load('/static/imgs/20210109142059972.png')
      guanluModels.wrapS = guanluModels.wrapT = THREE.RepeatWrapping
      guanluModels.repeat.set(1, 6)
      guanluModels.needsUpdate = true

      const material = new THREE.MeshPhongMaterial({ 
        transparent: true,
        side: THREE.DoubleSide,
        map: guanluModels
      });
      // 四给点坐标
      const pointArr = [
        [
          [-350, 5, 1634],
          [-285, 5, 485],
          [130, 5, 489],
          [128, 5, 475]
        ],
        [
          [-340, 5, 1491],
          [-297, 5, 1490]
        ],
        [
          [-313, 5, 1023],
          [-7.6, 5, 1028],
          [-6.9, 5, 991],
        ],
        [
          [-214, 5, 1021],
          [-214, 5, 990]
        ],
        [
          [-186, 5, 487],
          [-185, 5, 536]
        ],
        [
          [56, 5, 491],
          [57, 5, 536]
        ],
        [
          [-52, 5, 468],
          [-50, 5, 421]
        ],
        [
          [378, 5, 474],
          [-280, 5, 464],
          [-266, 5, -59],
          [-551, 5, -75],
          [-498, 5, -954],
          [258, 5, -1392]
        ],
        [
          [-431, 5, -68],
          [-431, 5, -27]
        ],
        [
          [-112, 5, -1176],
          [-156, 5, -1243]
        ]
      ]
      pointArr.map(res => {
        let points = [];
        res.map(res1 =>{
          points.push(new THREE.Vector3(
            ...res1
          ))
        })
        
        const curve = new THREE.CatmullRomCurve3( points, false, 'catmullrom', 0.000000001 )
        const geometry = new THREE.TubeGeometry( curve, 100, 8, 50, false)
        const line = new THREE.Mesh(geometry, material)
        sceneGroup.add(line)
      })
    },
    //标签
    createLableSprite_jcy() {
      this.buildNode.map(res =>{
        let spriteMaterial = new THREE.SpriteMaterial({
          map: new THREE.TextureLoader().load(`/static/imgs/${res.img}.png`),
          transparent: true,
          depthWrite: true
        });
        let sprite = new THREE.Sprite(spriteMaterial);
        sprite.scale.set(210, 53, 1);
        sprite.position.set(res.x, 200, res.z)
        sceneGroup.add(sprite);

        //标注线
        let points = [
          [res.x, 175, res.z],
          [res.x, 40, res.z]
        ];
        for (let i = 0; i < points.length; i++) {
          points[i] = new THREE.Vector3(points[i][0], points[i][1], points[i][2]);
        }
        let path = new THREE.CatmullRomCurve3(points);
        let geometry = new THREE.TubeGeometry(path, 50, 1, 10, false);
        let material = new THREE.MeshBasicMaterial({
          color: 0xffffff
        });
        let line = new THREE.Mesh(geometry, material);
        sceneGroup.add(line);
      })
    },
    //鼠标移动
    onMousemove(){
      this.lastMove = new Date().getTime()
    },
    //鼠标停止移动
    listenMouseMove(){
      var now = new Date().getTime()
      if( now - this.lastMove > this.lockTime ){
        scene.rotation.y += 0.003;
        // camera.position.set(this.cameraPosition.x, this.cameraPosition.y, this.cameraPosition.z)
        // camera.lookAt(0, 0, 0)
        // controls.target.set(0, 0, 0)
      }
    },
  }
}
</script>
<style scoped>
  .container{
    width: 100%;
    height: 100%;
    background-color: #000;
    position: fixed;
    top: 0;
    left: 0;
  }
  .loading{
    color: #fff;
  }
  #webgl{
    width: 100%;
    height: 100%;
  }
  .bg{
    top: 0;
    left: 0;
    position: fixed;
    width: 5120px;
    height: 5120px;
    background-image: linear-gradient(#3968A0,#3968A0, #3968A0);
  }
</style>



